.widget {
  margin-bottom: 2rem;
}

.widget:last-child {
  margin-bottom: 0;
}

.widget__label {
  display: block;
  margin-bottom: 0.25rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-pack: justify;
      justify-content: space-between;
  text-transform: uppercase;
  font-weight: bold;
  opacity: 0.8;
  font-size: 0.75rem;
}

.widget__label span {
  display: inline-block;
  margin-left: 0.5rem;
  opacity: 0.3;
}

.widget__input {
  margin: 0;
  width: 100%;
  display: block;
  margin-bottom: 0.25rem;
}

.widget__info {
  display: block;
  opacity: 0.5;
}



