.header {
  padding: 1rem 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.header__title {
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.1rem;
  margin-bottom: 0;
}

.header__button {
  border: 0;
  padding: 0.25rem 0.5rem;
  min-width: 0;
  background: rgba(255, 255, 255, 0.15);
}



