.collection-list{
  margin: 0;
  padding: 0;
  list-style: none;
}

.collection-list__item{
  display: block;
  padding: 0.5rem 0;
  text-decoration: none;
  color: inherit;
  opacity: 0.7;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.collection-list__item:hover{
  opacity: 1;
}



