body {
  margin: 0;
  padding: 0;
  font-size: 1em;
  line-height: 1.5;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6,
hgroup,
ul,
ol,
dd,
p,
figure,
pre,
table,
fieldset,
hr
.last {
  margin: 0;
  margin-bottom: 1.5rem;
}

h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
hgroup:last-child,
ul:last-child,
ol:last-child,
dd:last-child,
p:last-child,
figure:last-child,
pre:last-child,
table:last-child,
fieldset:last-child,
hr:last-child {
  margin-bottom: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

textarea,
input,
select,
button {
  font: inherit;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 100%;
  margin: 0;
  padding: 0.5rem;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid #ccc;
  border-radius: 0;
  outline: none;
}

button[disabled],
input[disabled],
select[disabled] {
  opacity: 0.3;
  cursor: not-allowed;
}

button:focus,
input:focus,
select:focus {
  border-color: rgba(0, 0, 0, 0.4);
}

textarea {
  resize: vertical;
}

select {
  padding-right: calc(0.5rem + 12px + 0.5rem);
  background-image: url('data:image/svg+xml,%3Csvg%20version%3D%221.1%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20xmlns%3Axlink%3D%22http%3A//www.w3.org/1999/xlink%22%20x%3D%220%22%20y%3D%220%22%20width%3D%22512%22%20height%3D%22512%22%20viewBox%3D%220%200%20512%20512%22%20xml%3Aspace%3D%22preserve%22%3E%3Cpath%20fill%3D%22currentColor%22%20d%3D%22M256%2C298.3L256%2C298.3L256%2C298.3l174.2-167.2c4.3-4.2%2C11.4-4.1%2C15.8%2C0.2l30.6%2C29.9c4.4%2C4.3%2C4.5%2C11.3%2C0.2%2C15.5L264.1%2C380.9c-2.2%2C2.2-5.2%2C3.2-8.1%2C3c-3%2C0.1-5.9-0.9-8.1-3L35.2%2C176.7c-4.3-4.2-4.2-11.2%2C0.2-15.5L66%2C131.3c4.4-4.3%2C11.5-4.4%2C15.8-0.2L256%2C298.3z%22/%3E%3C/svg%3E');
  background-size: 12px;
  background-repeat: no-repeat;
  background-position: calc(100% - 0.5rem) center;
}

select::-ms-expand {
  display: none;
}

input[type=checkbox] {
  display: block;
  width: 1rem;
  position: relative;
}

input[type=checkbox]:checked:after {
  content: '';
  width: 75%;
  height: 75%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: currentColor;
}

button {
  display: inline-block;
  width: auto;
  min-width: 6rem;
  cursor: pointer;
  background: #222;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75rem;
}

.button--subtle {
  background-color: #aaa;
  color: #222;
}

table {
  width: auto;
  border-collapse: collapse;
}

thead {
  background: #666;
  color: #fff;
  text-align: left;
}

tr {
  padding: 0;
  margin: 0;
}

td,
th {
  font-weight: normal;
  padding: 0.5rem;
  border: 1px solid rgba(0, 0, 0, 0.1);
}



